$메인칼라 : black;

.wow-enter {
    opacity: 0;
}

.wow-enter-active {
    opacity: 1;
    transition: all 500ms;
}



.red{
    color : $메인칼라;
}

@mixin 함수() {
    background : #5a5a5a;
    padding : 20px;
    border-radius : 5px;
    max-width: 500px;
    width : 100%;
    margin : auto;
}

.my-alert {
    @include 함수()
}
.my-alert2 {
    @extend .my-alert;
    background : #363636;
    color:#ffffff;
    margin-top:20px;
    height: 80px;
}
.my-alert-text {
    padding-top: 7px;
}

.my-alert3 {
    @extend .my-alert;
    background : #acacac;
    color:#ffffff;
}



